<template>
  <TreeSelect
    class="org-select"
    :value="value"
    :label="label"
    :placeholder="$t_p('placeholder')"
    @input="onSelectionChange"
    :noResultsText="$t_p('noResultsText')"
    :multiple="multiple"
    :options="treeData"
  ></TreeSelect>
</template>

<script>
import {getAreaList, getUserAreaList} from '@/api/area'
import TreeSelect from '@riophae/vue-treeselect'

import '@riophae/vue-treeselect/dist/vue-treeselect.css'

import './org-select.less'

export default {
  name: 'org-select',
  i18n_prefix: 'component.org-select',
  data () {
    return {
      treeData: []
    }
  },
  props: {
    value: [String, Array],
    multiple: Boolean,
    label: [String, Array],
    includeSelf: Boolean
  },
  model: {
    prop: 'value',
    event: 'selection'
  },

  methods: {
    loadTreeData () {
      (this.includeSelf ? getAreaList : getUserAreaList)({
        level: 99
      })
        .then(res => {
          let batchdata1 = res.data
          let dataValueBatch1 = batchdata1 =>
            batchdata1.map(({id, children, orgName}) => {
              let item = {
                id: id + '',
                label: orgName

              }
              if (children && children.length) {
                item.children = dataValueBatch1(children)
              }
              return item
            })
          this.treeData = dataValueBatch1(batchdata1)
          // console.log(12,this.treeData)
        })
    },

    onSelectionChange (v) {
      this.$emit('selection', v)
      this.$emit('on-selection-change', v)
    }
  },
  mounted () {
    this.loadTreeData()
  },
  destroyed () {
  },
  components: {
    TreeSelect
  }
}
</script>

<style lang="less">
</style>

<style lang="less" scoped>
  @import "../../style/varient";
.vue-treeselect__menu{
  width: 280px !important;
}
  .org-select {
    color: #000;
    font-size: @size-font-normal;
    height: @size-font-lg * 2;
  }
</style>



