<template>
  <div class="role-edit">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="btn-back"
               @click="$router.push({name:'role-list',query:{pageIndex:pageIndex,pageSize:pageSize,currentIndex:currentIndex}})">
            <Icon type="ios-arrow-back" />
          </div>
          <div class="breadcrumb-item"
               @click="$router.push({name:'role-list',query:{pageIndex:pageIndex,pageSize:pageSize,currentIndex:currentIndex}})">
            角色列表
          </div>
          <div class="breadcrumb-item-line">/</div>
          <div class="breadcrumb-item-link"
               v-if="!roleId">新增角色</div>
          <div class="breadcrumb-item-link"
               v-else>编辑角色</div>
          <span class="empty"></span>

        </div>
      </div>
      <div class="head-top">
        <Form ref="roleForm"
              class="role-form form"
              :model="roleInfo"
              :rules="roleInfoRules"
              :label-width="$flexible(110)">
          <div class="title">基本信息</div>
          <FormItem prop="roleName"
                    class="form-item"
                    label="角色名称">
            <Input class="form-input"
                   :maxlength="50"
                   v-model="roleInfo.roleName"></Input>
          </FormItem>
          <FormItem prop="roleType"
                    class="form-item"
                    label="角色类型">
            <RoleTypeSelect class="form-input"
                            v-model="roleInfo.roleType"
                            @on-selection-change="roleChange"></RoleTypeSelect>
          </FormItem>
          <FormItem prop="orgId"
                    class="form-item form-org"
                    label="所属区域"
                    id="org">
            <OrgSelect class="form-input"
                       v-model="roleInfo.orgId"
                       @on-selection-change="orgChange"></OrgSelect>
            <div class="red"
                 v-show="showOrg">请选择区域</div>
          </FormItem>
          <FormItem prop="remark"
                    class="form-item"
                    label="角色描述">
            <Input :maxlength="50"
                   class="form-input"
                   v-model="roleInfo.remark"></Input>
          </FormItem>
          <FormItem prop="loginPlatforms"
                    class="form-item"
                    label="登陆权限">
            <checkbox-group class="form-input"
                            v-model="roleInfo.loginPlatforms">
              <Checkbox label="1">WEB</Checkbox>
              <Checkbox label="2">微信</Checkbox>
            </checkbox-group>
          </FormItem>
        </Form>
      </div>

      <div class="wrapper">
        <div class="left">
          <div class="title">
            菜单权限
            <span class="red"
                  v-show="moduleShow">请输入搜索内容</span>
          </div>
          <div class="block">
            <div class="area-tree">
              <Tree show-checkbox
                    ref="area_tree"
                    @on-select-change="onTreeSelectChange"
                    @on-check-change="onTreeCheckChange"
                    :data="treeData"></Tree>
            </div>
          </div>
        </div>
        <div class="right">
          <div class="title">
            功能权限
            <span class="sm"> {{currentSelectModule && currentSelectModule.name}}</span>
          </div>
          <div class="block">
            <div class="user-table no-scroll">
              <Table class="m-table"
                     style="overflow-x: hidden;"
                     stripe
                     @on-selection-change="saveCurrentModuleLocal"
                     :border="true"
                     :columns="tableColumns"
                     :data="tableData"
                     no-data-text="空"></Table>
            </div>
          </div>
        </div>
      </div>
      <div class="role-ops">
        <Button class="btn"
                size="large"
                type="primary"
                @click="$router.push({name:'role-list'})">{{$t('common.message.cancel')}}
        </Button>
        <Button class="btn"
                size="large"
                :loading="isBtnLoading"
                style="margin-left: 40px"
                type="default"
                @click="saveRole">{{$t('common.message.save')}}
        </Button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  addRole,
  editRole,
  getPermissionList,
  getRoleInfo,
  getRoles
} from '@/api/role'
import RoleTypeSelect from '@c/role-type-select'
import OrgSelect from '@c/org-select'

export default {
  name: 'role-edit',
  data () {
    return {
      treeData: [],
      users: [],
      tableColumns: [
        {
          type: 'selection',
          width: 80,
          align: 'center'
        },
        {
          title: '模块名称',
          key: 'moduleName',
          align: 'center'
        },
        {
          title: '备注',
          key: 'remark'
        }
      ],
      currentCanUseOps: [],
      currentSelectModule: null,
      tableData: [],
      showOrg: false,
      moduleShow: false,
      roleInfo: {
        roleName: '',
        roleType: '',
        remark: '',
        modules: [],
        orgId: undefined,
        loginPlatforms: [],
        loginPlatform: Number
      },
      num: 1,
      // 操作权限map
      opModuleMap: {},
      roleInfoRules: {
        roleName: [
          {
            required: true,
            message: '请输入角色名称',
            trigger: 'blur'
          }
        ],
        orgId: [
          {
            required: true,
            trigger: 'change',
            validator: (rule, value, callback) => {
              let d = document.getElementsByClassName(
                'vue-treeselect__control'
              )[0]
              let icon = document.getElementsByClassName(
                'vue-treeselect__control-arrow'
              )[0]

              if (value) {
                d.style.border = '1px solid #dcdee2'
                icon.style.color = '#dcdee2'
                callback()
              } else {
                icon.style.color = '#ed4014'
                d.style.border = '1px solid #ed4014'
                this.showOrg = true
                callback()
              }
            }
          }
        ],
        loginPlatforms: [
          {
            required: true,
            type: 'array',
            min: 1,
            message: '请选择登录权限',
            trigger: 'change'
          },
          {
            type: 'array',
            max: 2,
            message: this.$t_p('login_permission_tip'),
            trigger: 'change'
          }
        ],
        roleType: [
          {
            required: true,
            message: '请选择角色类型',
            trigger: 'change'
          }
        ]
      },

      isBtnLoading: false
    }
  },
  methods: {
    roleChange (data) {},
    orgChange (data) {
      if (data != '' || data != null) {
        this.showOrg = false
        let dom = document.getElementsByClassName('vue-treeselect__control')[0]
        let icon = document.getElementsByClassName(
          'vue-treeselect__control-arrow'
        )[0]
        dom.style.border = '1px solid #dcdee2'
        icon.style.color = '#616161'
      }
    },
    isMatchRoleType (module) {
      let manager = (module.manager || '').split(',')
      return manager.includes(this.roleInfo.roleType + '')
    },
    parseTreeData (rdata, level = 0, selectedModules = []) {
      let treeData = {
        title: rdata.moduleName,
        moduleId: rdata.moduleId,
        moduleCode: rdata.moduleCode,
        moduleName: rdata.moduleName,
        name: rdata.moduleName,
        operations: [],
        expand: level < 2,
        children: [],
        render: (h, { root, node, data }) => {
          return h(
            'span',
            {
              style: {
                cursor: 'pointer',
                lineHeight: '1.4em'
              },
              on: {
                click: () => {
                  this.onTreeSelectChange([data])
                }
              }
            },
            [
              h('Icon', {
                style: {
                  color: '#2e8cf0',
                  verticalAlign: 'middle',
                  fontSize: '1.1',
                  marginRight: '3px'
                }
                // props: {
                //   type: data._checked ? 'md-checkbox' : 'md-square-outline'
                // }
              }),
              h(
                'span',
                {
                  style: {
                    color: '#fff',
                    verticalAlign: 'middle'
                  }
                },
                data.title
              )
            ]
          )
        }
      }

      if (rdata.children && rdata.children.length) {
        treeData.children = rdata.children.map((sd) =>
          this.parseTreeData(sd, level + 1, selectedModules)
        )
      }

      if (rdata.operations) {
        rdata.operations.forEach((op) => {
          treeData.operations.push(op)
          if (selectedModules.includes(op.moduleId)) {
            treeData.checked = true
          }
          this.opModuleMap[op.moduleId] = op
          this.currentCanUseOps.push(op.moduleId)
        })
      }

      return treeData
    },
    loadTreeData () {
      getPermissionList({}).then((res) => {
        this.treeRawData = res.data || []
        this.refreshTree(true)
      })
    },
    refreshTree (select) {
      this.currentCanUseOps = []
      this.treeData = this.treeRawData.map((d) =>
        this.parseTreeData(d, 0, this.roleInfo.modules)
      )
    },
    onTreeCheckChange (nodes) {
      if (nodes.length > 0) {
        this.moduleShow = false
      }
      let availableOps = []
      nodes.forEach((node) => {
        let allNotSelected = true
        let ops = []
        node.operations &&
          node.operations.forEach((op) => {
            op.moduleId != null && availableOps.push(op.moduleId)
            if (this.roleInfo.modules.includes(op.moduleId)) {
              allNotSelected = false
            }
            ops.push(op.moduleId)
          })
        if (allNotSelected) {
          this.roleInfo.modules = this.roleInfo.modules.concat(ops)
        }
      })

      let md = [].concat(this.roleInfo.modules)
      for (let i = md.length - 1; i >= 0; i--) {
        if (!availableOps.includes(md[i])) {
          md.splice(i, 1)
        }
      }
      this.roleInfo.modules = md
      console.log('----------------', this.currentSelectModule)
      if (this.currentSelectModule) {
        this.tableData = (this.currentSelectModule.operations || []).map(
          (op) => {
            return {
              ...op,
              _checked: this.isOpSelected(op)
            }
          }
        )
      }
    },
    onTreeSelectChange (nodes) {
      console.log('=================', nodes)
      if (nodes.length > 0) {
        this.currentSelectModule = {
          ...nodes[0]
        }
        this.tableData = (this.currentSelectModule.operations || []).map(
          (op) => {
            return {
              ...op,
              _checked: this.isOpSelected(op)
            }
          }
        )
      } else {
        this.currentSelectModule = null
        this.tableData = []
      }
    },
    loadRoleInfo () {
      if (this.roleId != null) {
        getRoleInfo({ roleId: this.roleId }).then((res) => {
          let newData = res.data || {}
          let loginPlatforms = []
          if (newData.loginPlatform == 3) {
            loginPlatforms = ['1', '2']
          } else if (newData.loginPlatform == 1) {
            loginPlatforms = ['1']
          } else if (newData.loginPlatform == 2) {
            loginPlatforms = ['2']
          }
          this.roleInfo = {
            ...newData,
            orgId: newData.orgId == null ? '' : newData.orgId + '',
            roleType: newData.roleType == null ? '' : newData.roleType + '',
            loginPlatforms
          }
          if (this.treeData.length) {
            this.refreshTree()
          }
        })
      }
    },
    getUsers () {
      if (!this.roleId) {
        return
      }
      getRoles({
        roleId: this.roleId
      }).then((res) => {
        this.users = res.data || []
      })
    },
    isOpSelected (op) {
      return (this.roleInfo.modules || []).includes(op.moduleId)
    },
    saveCurrentModuleLocal (nodes) {
      // console.log(nodes)
      if (!this.roleInfo.modules) {
        this.roleInfo.modules = []
      }

      let modules = [...this.roleInfo.modules]

      // 先清空
      this.currentSelectModule.operations.forEach((op) => {
        if (modules.includes(op.moduleId)) {
          modules.splice(modules.indexOf(op.moduleId), 1)
        }
      })

      // 再加入
      nodes.forEach((n) => {
        if (!modules.includes(n.moduleId)) {
          modules.push(n.moduleId)
        }
      })

      this.roleInfo.modules = modules

      this.refreshTree()
    },
    checkRoleFrom () {
      // let me = this;
      return new Promise((resolve, reject) => {
        this.$refs.roleForm.validate((valid, b) => {
          if (valid) {
            resolve()
          } else {
            // console.log(b)
            reject(valid, b)
          }
        })
      })
    },
    saveRole () {
      this.roleInfo.modules.length == 0
        ? (this.moduleShow = true)
        : (this.moduleShow = false)

      this.checkRoleFrom().then(
        () => {
          this.isBtnLoading = true
          let modules = []
          ;(this.roleInfo.modules || []).forEach((m) => {
            if (this.currentCanUseOps.includes(m)) {
              modules.push(m)
            }
          })

          if (this.roleId) {
            let sum = 0
            this.roleInfo.loginPlatforms.map((v) => (sum = Number(v) + sum))
            this.roleInfo.loginPlatform = sum
            editRole({
              ...this.roleInfo,
              roleId: this.roleId,
              modules
            })
              .then(
                () => {
                  this.$Message.success(this.$t('common.message.save_success'))
                  this.$router.replace({ name: 'role-list' })
                  this.loadRoleInfo()
                },
                (err) => {
                  this.$Message.warning(
                    err.serverMessage || this.$t('common.message.save_fail')
                  )
                }
              )
              .finally(() => {
                this.isBtnLoading = false
              })
          } else {
            let sum = 0
            this.roleInfo.loginPlatforms.map((v) => (sum = Number(v) + sum))
            this.roleInfo.loginPlatform = sum
            addRole({
              ...this.roleInfo,
              modules
            })
              .then(
                () => {
                  this.$Message.success(this.$t('common.message.add_success'))
                  this.$router.replace({ name: 'role-list' })
                  // this.$router.replace({name: 'roleList'})
                },
                (err) => {
                  this.$Message.warning(
                    err.serverMessage || this.$t('common.message.save_fail')
                  )
                }
              )
              .finally(() => {
                this.isBtnLoading = false
              })
          }
        }
        // (valid, b) => {
        //   this.isBtnLoading = false;
        //   this.$Message.error("请检查必填项是否正确填写");
        // }
      )
    }
  },
  watch: {
    roleId (v) {
      if (v != null) {
        this.loadRoleInfo()
      }
    }
  },
  computed: {
    roleId () {
      return this.$route.params.roleId
    }
  },
  mounted () {
    this.loadTreeData()
    this.loadRoleInfo()
    // this.getUsers()
  },
  components: {
    RoleTypeSelect,
    OrgSelect
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient.less';

@size-padding: 20px;
@size-width-left: 400px;
@size-form-height: 60px;
.form-org {
  position: relative;
  .red {
    position: absolute;
    left: 0px;
    top: 30px;
    color: #ed4014;
  }
}
.role-edit {
  width: 100%;
  margin-bottom: 20px;

  .content {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 15px;

    .head-top {
      width: 100%;
      height: auto;
      display: flex;

      .role-form {
        width: 40%;

        .title {
          font-size: 1.2em;
          line-height: 2.2;
          margin-bottom: 10px;
        }
        .form-item {
          margin-bottom: 20px;
          /deep/ .role-select{
            .ivu-select-selection{
              width: 280px;
            }
          }
        }
      }
    }

    .wrapper {
      position: relative;
      height: 420px;
      padding: 0 20px 20px 20px;
      // background-color: #fafafa;
      background-color: #000;
      border-radius: 5px;
      // border: solid 1px #dcdcdc;
      border: none;
      margin-bottom: 30px;
      display: flex;

      .left,
      .right {
        height: 100%;

        padding: 0 8px;
        /*position: relative;*/
        overflow: visible;

        .title {
          padding-left: 8px;
          // color: #1b1b1b;
          color: #fff;
          font-size: @size-font-lg;
          font-weight: bold;
          line-height: 48px;

          .red {
            color: #ed4014;
            font-size: 14px;
            font-weight: normal;
          }

          .sm {
            font-weight: normal;
          }
        }

        .title::before {
          content: '*';
          color: #ed4014;
          font-size: 14px;
          font-weight: normal;
          font-family: SimSun;
        }

        .block {
          // background-color: white;
          background-color: #262C3A;
          border-radius: 5px;
          // border: solid 1px @color-border-grey-1;
          box-shadow: #dcdcdc 0 0 1px;
          padding: 20px;
          height: 330px;
          overflow-y: scroll;
          .ivu-table-wrapper{
            border: 1px solid #000;
            /deep/.ivu-table-border th, .ivu-table-border td {
              border-right: 1px solid #000;
            }
          }

        }

        .ops {
          position: absolute;
          top: 8px;
          right: 8px;

          .op-btn {
            margin-left: 10px;
            line-height: 1.2;
          }
        }
      }

      .left {
        float: left;
        width: @size-width-left;
        position: relative;
        overflow-y: auto;

        .area-tree {
          margin-left: 30px;
          margin-right: 30px;

          .ivu-tree .ivu-icon:before {
            color: #000;
          }
        }
      }
      .right {
        width: calc(~'100% - ' @size-width-left);
        left: @size-width-left;
        padding-left: 20px;
      }
    }

    .role-ops {
      display: flex;
      text-align: center;
      padding-top: 30px;
      border-top: 1px solid @color-border-grey-1;

      .btn {
        min-width: 100px;
      }
    }
  }
}
</style>



