<template>
  <Select class="role-select" :value="value" :placeholder="placeholder" @input="onSelectionChange">
    <Option value="" key="-1" v-show="show">{{$t('common.text.all')}}</Option>
    <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.label }}</Option>
  </Select>
</template>

<script>

import {Select} from 'iview'
import {RoleTypeNameMap} from '@/libs/constants'

export default {
  name: 'role-type-select',
  data () {
    return {
      options: []
    }
  },
  props: {
    value: String,
    placeholder: String,
    show: Boolean
  },
  model: {
    prop: 'value',
    event: 'selection'
  },
  methods: {
    loadTreeData () {
      this.options = Object.keys(RoleTypeNameMap).map((key) => {
        return {
          value: key,
          label: RoleTypeNameMap[key]
        }
      })
    },
    onSelectionChange (v) {
      this.$emit('selection', v)
      this.$emit('on-selection-change', v)
    }
  },
  mounted () {
    this.loadTreeData()
  },
  components: {
    Select
  }
}
</script>

<style scoped lang="less">

/deep/.ivu-select-dropdown{
  min-width: 280px !important;
}
  .role-select {
    color: #000;
  }
</style>



